import html2canvas from 'html2canvas';
import React, { useRef } from 'react';
import AccessoriesGrid from './AccessoriesGrid';
import ClownContainer from './ClownContainer';


function App() {
  const clownContainerRef = useRef(null);

  const handleDownload = async () => {
    const clownContainer = clownContainerRef.current;

    if (!clownContainer) {
      return;
    }

    const rect = clownContainer.getBoundingClientRect();
    const { width, height } = rect;

    // Convert clownContainer to an image using html2canvas
    const canvas = await html2canvas(clownContainer, { width, height });

    // Get the data URL of the canvas image
    const dataUrl = canvas.toDataURL();

    // Create a download link
    const downloadLink = document.createElement('a');
    downloadLink.href = dataUrl;
    downloadLink.download = 'doll_image.png';

    // Trigger the download
    downloadLink.click();
  };

  return (
    <main>
      <div id="header">
        <h1>WELCOME TO THE CLOWN ZONE</h1>
      </div>

      <div id="wrapper">
        <div id="clownZone">
          <ClownContainer ref={clownContainerRef}/>
        </div>

        <div id="accessories">
          <h2>Accessories</h2>
          <AccessoriesGrid />
        </div>

        <button onClick={handleDownload}>Download Clown!</button>
      </div>
    </main>
  );
};

export default App;